/* Header */
.header {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    justify-content: start;
    padding: 8px;
    background: transparent;
    /* box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.70), 0 2px 10px 0 rgba(0, 0, 0, 0.12); */
    position: fixed;
    top: 0;
    left: 0;
    z-index: 200;
    width: 100%;
    height: 70px;
    transition: background-color .2s .4s, transform .4s;
    transform: translateY(0);
    will-change: transform;
}

.headerSticky {
    background-color: #141b2b;
    transition: background-color 0ms .4s, transform .4s;
    transform: translateY(0);
    box-shadow: 0 2px 4px rgba(20, 27, 43, 0.6);
}

.headerHidden {
    background-color: #141b2b;
    transform: translateY(-92px);
    transition: background-color 0ms .4s, transform .4s;
}

@media (min-width: var(--bp-desktop)) {
    header {
        height: 90px;
    }
}

.header_container {
    padding: 0 4vw;
    width: 100%;
    height: 100%;
}

@media (min-width: var(--bp-desktop)) {
    .header_container {
        padding: 0 30px;
    }
}

#logo {
    height: 100px;
    width: 100px;
    margin: 10px 20px 0 100px;
    /*in future: background-image: url("../media/Wappen_modern_SW_minimalistisch.svg" param(--color var(--primary-color)));*/
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
}

.nav-list {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    justify-content: start;
    padding: 8px;
}

